/*Desktop View*/
.logo-nav {
  display: flex;
  align-items: center;
  /* font-weight: 550; */
  justify-content: space-between;
  max-width: 1024px;
  margin: 10px auto;
  /* background-color: #1f1f1f; */
}

.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  background-color: #1f1f1f;
  justify-content: space-between;
  box-shadow: 0 1px 4px rgba(146, 161, 176, .15);
  height: 5vh;
}

.header * {
  background-color: #1f1f1f;
  color: #eee;
}

.logo-nav a {
  text-decoration: none;
  /* color: #0E2431; */
  font-size: 20px;
}

.nav-options {
  padding-left: 15px;
  display: flex;
  grid-gap: 25px;
  list-style-type: none;
}

.nav-options a {
  text-decoration: none;
  /* color: rgb(8, 8, 8); */
  margin-right: 20px;
}

.left-logo:hover, .nav-options, a:hover {
  color: #e04f62;
  /* font-size: 1.5rem; */
  /* transition: all 0.5s ease; */
}

.mobile-option {
  display: none;
}

.mobile-nav {
  display: none;
}

.right-navigations {
  display: flex;
  align-items: center;
  justify-content: center;
}

.theme-toggle {
  cursor: pointer;
}

